export const breakpoints = {
  hdesktop: "@media all and (min-width: 1440px)",
  desktop: "@media all and (min-width: 1024px)",
  tablet: "@media all and (min-width: 768px)",
  phone: "@media all",
  onlyhdesktop: "@media all and (min-width: 1440px)",
  onlydesktop: "@media all and (min-width: 1024px) and (max-width: 1439px)",
  onlytablet: "@media all and (min-width: 768px) and (max-width: 1023px)",
  onlyphone: "@media all and (max-width: 767px)"
};

export const breakpoint = breakpoints;



