import css from "styled-components";
import React from "react";
import { bool, string } from "prop-types";

import { color, font, radius, setSpace, setType, time } from "../../../utils";

const Input = css.input`
  ${setSpace("phm")};
  ${setSpace("pvm")};
  ${setType("x")};
  border-radius: ${radius.m};
  border: 1px solid ${color.greyHL};
  box-shadow: none;
  color: ${({ disabled }) => (disabled ? color.greyBlk : color.blueBlk)};
  font-family: ${font.serif};
  position: relative;
  resize: none;
  transition: border-color ${time.m};
  width: 100%;
  &:focus {
    outline: none;
    border-color: ${color.blueM};
    z-index: 50;
  }
  &::placeholder {
    ${setType("x")};
    color: ${color.greyLLt};
    font-family: ${font.serif};
    font-style: italic;
  }
  ${({ valid }) =>
    valid === false
      ? `
    border-color: ${color.redM} !important;
    z-index: 50;
  `
      : ``};
  ${({ area }) =>
    area
      ? `
    min-height: 100px;
  `
      : ``}
  ${({ place }) => {
    if (place === "left") {
      return `border-radius: ${radius.m} 0 0 ${radius.m}; left: 1px`;
    } else if (place === "right") {
      return `border-radius: 0 ${radius.m} ${radius.m} 0; right: 1px`;
    }
    if (place !== null) {
      return `border-radius: 0`;
    }
    return null;
  }};
  ${({ nooffset }) => (nooffset ? `right: 0; left: 0;` : ``)};
`;

const Textarea = Input.withComponent("textarea");

const TextInput = (props) =>
  props.area ? <Textarea {...props} /> : <Input {...props} />;

TextInput.propTypes = {
  area: bool,
  input: bool,
  nooffset: bool,
  place: string
};

TextInput.defaultProps = {
  area: false,
  input: false,
  nooffset: false,
  place: null
};

export default TextInput;



